import React, { Component } from "react";
import DatePicker from "react-date-picker";
import "./login.css";
import dataLayer from "../../../data-laye";
import PhoneInput from "react-phone-number-input";
import "react-phone-number-input/style.css";
import dataLaye from "../../../data-laye";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userName: "",
      password: "",
    };
    this.dataLayer = dataLayer;
  }

  setPassword = async (event) => {
    event.persist();
    const value = event.target.value;
    if (event) {
      this.setState(() => {
        return {
          password: value,
        };
      });
    }
  };

  setEmail = async (event) => {
    event.persist();
    const value = event.target.value;
    if (event) {
      this.setState(() => {
        return {
          userName: value,
        };
      });
    }
  };

  logIn = async (event) => {
    const userName = this.state.userName;
    const password = this.state.password;

    if (userName == "test@test.com" && password == "test007!") {
      console.log(userName, password);
    } else {
      toast.error("Invelid Credantials...", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  render() {
    return (
      <section className="login-user">
        <ToastContainer
          position="top-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        {/* Same as */}
        <ToastContainer />
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-6 user-info-bg">
              <div className="login-logo">
                <img src="../../../images/logo.png" />
              </div>
            </div>
            <div className="col-md-6 user-info-details">
              <div className="signup-wrap">
                <span className="signup-text">
                  <span>Need an Account? </span>
                  <a href="/signup">Sign Up</a>
                </span>
              </div>
              <div className="user-info-details-login">
                <div className="language-selector">
                  <h3>Login</h3>
                  <div className="dropdown">
                    <button
                      className="btn btn-primary dropdown-toggle"
                      type="button"
                      id="dropdownMenuButton"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                      English
                    </button>
                    <div
                      className="dropdown-menu"
                      aria-labelledby="dropdownMenuButton"
                    >
                      <a className="dropdown-item" href="#">
                        English
                      </a>
                      <a className="dropdown-item" href="#">
                        Portugues
                      </a>
                      <a className="dropdown-item" href="#">
                        Deutsch
                      </a>
                      <a className="dropdown-item" href="#">
                        FRANAIS
                      </a>
                      <a className="dropdown-item" href="#">
                        
                      </a>
                      <a className="dropdown-item" href="#">
                        
                      </a>
                      <a className="dropdown-item" href="#">
                        ITALIANO
                      </a>
                    </div>
                  </div>
                </div>
                <form className="user-login-form mt-5">
                  <div className="form-group">
                    <input
                      type="text"
                      className="form-control effects"
                      id="formGroupExampleInput"
                      value={this.state.userName}
                      onChange={this.setEmail}
                      placeholder="Email"
                    />
                    <div className="line"></div>
                  </div>
                  <div className="form-group">
                    <input
                      type="password"
                      className="form-control effects"
                      id="formGroupExampleInput2"
                      value={this.state.password}
                      onChange={this.setPassword}
                      placeholder="Password"
                    />
                    <div className="line"></div>
                  </div>
                  <button
                    type="button"
                    onClick={this.logIn}
                    className="btn btn-primary mt-5"
                  >
                    Login
                  </button>
                  <span className="signup-text mt-3">
                    <a href="/forgotPassword">Forgot Password?</a>
                  </span>
                  <div className="login-with mt-3">Or</div>
                  <div className="login-social mt-5">
                    <div className="google-btn mb-3">
                      <button
                        type="button"
                        onClick={this.logIn}
                        className="btn btn-primary"
                      >
                        Google
                      </button>
                    </div>
                    <div className="micro-soft-btn mb-3">
                      <button type="button" className="btn btn-primary">
                        Microsoft
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Login;
